body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  justify-content: center;
  flex-flow: column;
  align-items: center;
  height: 100vh;
  font-size: 16px;
  background-image: linear-gradient(160deg, #0093e9 0%, #80d0c7 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#container {
  width: 80vh;
  border: 4px dashed rgba(0, 0, 0, 0);
  min-height: 140px;
  padding: 32px;
  position: relative;
  border-radius: 16px;
  -webkit-box-align: center;
  align-items: left;
  -webkit-box-pack: center;
  justify-content: left;
  flex-direction: column;
  text-align: left;
  word-break: break-word;
  background-color: #74ebd5;
  background-image: linear-gradient(90deg, #74ebd5 0%, #9face6 100%);
}

#walletButton {
  display: flex;
  justify-content: center;
  margin-left: 70%;
  margin-top: -2%;
}

input {
  border-radius: 1em;
  font-size: 1em;
  font-weight: bold;
  width: 110%;
  border-color: #254cdd;
  line-height: 32px;
  background-color: #4158d0;
  color: #ffffff;
  cursor: none;
  text-align: center;
  border: none;
  cursor: default;
}

#status {
  color: #254cdd;
  font-size: 14px;
  font-weight: 600;
}

button {
  padding: 9px 10px;
  max-height: 3em;
  *box-shadow: 4px 4px 15px 0 rgb(0 0 0 / 20%);
  border-color: #254cdd;
  border: none;
  background: none;
  color: white;
  background-color: #4158d0;
  border-radius: 16px;
  align-items: center;
  font-size: 16px;
  font-weight: 500;
  text-align: center;
  font-weight: bold;
  cursor: pointer;
  min-width: 160px;
  font-family: Rubik, sans-serif;
}

#logo {
  float: left;
  display: flex;
  margin-left: -10px;
  margin-top: -16px;
  width: 210px;
  margin-bottom: 20px;
}

#buttonsTable {
  display: flex;
  justify-content: space-between;
  margin-top: 1em;
  text-align: center;
}

#record {
  margin-left: 50px;
}

#publish {
  height: 36px;
}

h2,
h3 {
  display: block;
  clear: both;
  font-weight: 400;
  color: #ffffff;
}

a {
  color: #254cdd;
}

table td {
  width: 250px;
  color: #ffffff;
}

table {
  color: #ffffff;
}

#listContainer {
  margin-top: 60px;
  width: 80vh;
  border: 4px dashed rgba(0, 0, 0, 0);
  min-height: 140px;
  padding: 32px;
  position: relative;
  border-radius: 16px;
  -webkit-box-align: center;
  align-items: left;
  -webkit-box-pack: center;
  justify-content: left;
  flex-direction: column;
  text-align: left;
  word-break: break-word;
  background-color: #74ebd5;
  background-image: linear-gradient(90deg, #74ebd5 0%, #9face6 100%);
  color: #ffffff;
}

#codigo {
  width: 300px;
  height: 34px;
  margin-left: 50px;
}

::placeholder {
  color: #ffffff;
}

#div-load {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 3em;
  margin-top: 1em;
}

#div-load h5 {
  font-size: 1px;
}

#eth-icon {
  position: absolute;
  -webkit-animation: spin 2s linear infinite;
  -moz-animation: spin 2s linear infinite;
  animation: spin 2s linear infinite;
}
@-moz-keyframes spin {
  100% {
    -moz-transform: rotate(360deg);
  }
}
@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
  }
}
@keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

#form-token input {
  width: 90%;
  margin-top: 1.5em;
  border-radius: 0px;
  background-color: transparent;
  color: black;
  border-bottom: solid 1px;
}

#form-token button {
  color: white;
}

#form-div {
  display: flex;
  padding: 1em;
}

#form-token {
  display: flex;
}

#form-token p {
  margin-top: 3em;
  font-size: 1.1em;
}

#div-form-text-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 50%;
}

#div-form-input-container {
  width: 50%;
  margin: 0.5em;
}

.modal-content {
  border: none;
  background-color: transparent;
  user-select: none;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  background-color: #74ebd5;
  background-image: linear-gradient(90deg, #74ebd5 0%, #9face6 100%);
  border-radius: 1em;
  color: black;
}

.modal-footer {
  border: none;
}

::placeholder {
  color: black;
}

input {
  cursor: default;
  outline: none;
}
